/* ==========================================================================

   ### Visibility Helpers

   ```
   <p class="u-hidden">this is hidden from mobile screens</p>
   <p class="u-visuallyhidden">this is hidden from small tablet screens</p>
   <p class="u-invisible">this is hidden from large tablets screens</p>
   ```
   ========================================================================== */
.u-hidden,
.js .is-jshidden {
  display: none !important;
  visibility: hidden;
}

.u-visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.u-visuallyhidden .focusable:active, .u-visuallyhidden .focusable:focus {
  position: static;
  overflow: visible;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0;
}

.u-invisible {
  visibility: hidden;
}

@media (min-width: 30em) {
  .u-hidden--from-xs {
    display: none !important;
    visibility: hidden;
  }
}

@media (max-width: 29.99em) {
